if (document.querySelector('.history__slider')) {
  const swiperMassHistory = new Swiper('.history__slider', {
    speed: 800,
    slidesPerView: 'auto',
    navigation: {
      nextEl: '.history__slider-btn--next',
      prevEl: '.history__slider-btn--prev',
    },
    a11y: {
      prevSlideMessage: 'Назад',
      nextSlideMessage: 'Вперёд',
    },
  });

  const getEvery = (arr, gap) => Array.from({ length: Math.floor(arr.length / gap) }, (_, i) => arr[i * gap + gap - 1]);
  const slidesHistory = document.querySelectorAll('.history__slide');

  if (window.screen.width <= 768) {
    slidesHistory.forEach((el, index, arr) => {
      el.addEventListener('click', () => {
        const prevEl = el.previousElementSibling;
        const firstElIndex = index !== 0;
        const endElIndex = index !== Array.from(arr).length - 1;

        if (prevEl && prevEl.classList.contains('active') && endElIndex) {
          swiperMassHistory.slideNext();
        }
        if (el.classList.contains('active') && firstElIndex && endElIndex) {
          swiperMassHistory.slidePrev();
        }

        arr.forEach(el => el.classList.remove('active'));
        el.classList.add('active');
      });
    });
  } else {
    const everyFourStart = getEvery(slidesHistory, 4);
    const everyFourEnd = getEvery(Array.from(slidesHistory).reverse(), 4);

    everyFourStart.forEach(el => {
      el.addEventListener('mouseenter', () => {
        swiperMassHistory.slideNext();
      });
    });

    everyFourEnd.forEach(el => {
      el.addEventListener('mouseenter', () => {
        swiperMassHistory.slidePrev();
      });
    });

    document.querySelectorAll('.history__year').forEach((el, index) => {
      el.addEventListener('mouseenter', () => {
        slidesHistory.forEach(el => el.classList.remove('active'));
        slidesHistory[index].classList.add('active');
      });
    });
  }

  const swiperMassMedia = new Swiper('.mass-media__slider', {
    speed: 800,
    breakpoints: {
      769: {
        slidesPerGroup: 4,
        slidesPerView: 4,
        spaceBetween: '1.8%',
      },
      100: {
        slidesPerGroup: 1,
        slidesPerView: 'auto',
        spaceBetween: '5%',
      },
    },
    navigation: {
      nextEl: '.mass-media__slider-btn--next',
      prevEl: '.mass-media__slider-btn--prev',
    },
    a11y: {
      prevSlideMessage: 'Назад',
      nextSlideMessage: 'Вперёд',
    },
  });

  const swiperVacancies = new Swiper('.vacancies__slider', {
    speed: 800,
    breakpoints: {
      769: {
        slidesPerGroup: 4,
        slidesPerView: 4,
        spaceBetween: '1.8%',
      },
      100: {
        slidesPerGroup: 1,
        slidesPerView: 'auto',
        spaceBetween: '4.5%',
      },
    },
    navigation: {
      nextEl: '.vacancies__slider-btn--next',
      prevEl: '.vacancies__slider-btn--prev',
    },
    a11y: {
      prevSlideMessage: 'Назад',
      nextSlideMessage: 'Вперёд',
    },
  });
}
